@import "../../scss/variables.scss";
@import "../../scss/mixins.scss";

.cartItem {
  @include flexbox(space-between, center);
  width: 100%;
  border-bottom: solid 1px #ccc;
  padding: 0.5rem 0;

  .recipe {
    @include flexbox(center, center);
  }

  .details {
    width: 75%;
    margin-left: 1rem;
    line-height: 2rem;
  }

  .imageContainer {
    width: 100px;
    height: 100%;
    border-radius: 3px;
    overflow: hidden;
    cursor: pointer;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .quantity {
    @include flexbox(left, center);
    font-size: 1rem;
  }

  .price,
  .quantity {
    color: $color--six;
    font-size: 1rem;
  }

  .fas,
  .far {
    color: $color--six;
    cursor: pointer;
  }

  .cart__icon__arrow {
    margin: 0 1rem;
  }
}
