@import "../../scss/variables.scss";
@import "../../scss/mixins.scss";

.about {
  background-color: $color--five;

  .wrapper {
    @include wrapper;
    text-align: center;
  }

  .social__links {
    margin: 1rem 0;
  }

  .icon {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 0.1rem;
    margin: 1.2rem 0.8rem;
    transition: all 0.2s ease-out;
    cursor: pointer;
  }

  .icon:hover {
    transform: scale(1.1);
  }

  .agreement {
    margin: 1rem 0;
  }

  .terms {
    font-size: 0.7rem;
  }

  h2 {
    color: white;
  }

  p {
    color: #bdbdbd;
  }
}
