@import "../../scss/variables.scss";
@import "../../scss/mixins.scss";

.search {
  @include wrapper;
  background-color: white;
  width: 100%;
  min-height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;

  .form {
    @include flexbox(space-between, center);
    width: 70vw;
    margin: auto;
    margin-top: 5vh;
  }

  .field {
    width: 90%;
    border: none;
    border-bottom: solid 2px #ccc;
    outline: none;
    font-size: 1rem;
    padding: 0.5rem 0;
  }

  .fa-times-circle {
    margin-left: 1rem;
    color: $color--five;
  }
}
