@import "../../scss/mixins.scss";
@import "../../scss/variables.scss";

.navbar {
  @include flexbox(space-between, center);
  background-color: $color--one;
  color: $color--four;
  width: 100%;
  min-height: 8vh;
  padding: 1.5rem;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;

  .app__name {
    cursor: pointer;
  }

  .menu__icon {
    margin-left: 1.5rem;
    cursor: pointer;
  }

  .my__wishlist {
    margin-right: 2rem;
    cursor: pointer;
  }
}

@media screen and (max-width: 600px) {
  .navbar {
    .my__wishlist {
      display: none;
    }
  }
}
