@import "../../scss/variables.scss";

.primaryBtn {
  background-color: $color--two;
  border: none;
  border-radius: 0.2rem;
  color: $color--four;
  cursor: pointer;
  font-size: 0.95em;
  font-weight: bold;
  padding: 0.8rem 1.2rem;
  outline: none;
  box-shadow: 0 0 0.2rem 0 $color--six;
  margin-left: 0.5rem;
}

.secondaryBtn {
  background-color: $color--four;
  color: $color--one;
}

.primaryBtn:active,
.secondaryBtn:active {
  background-color: $color--one;
}
