@import "../../scss/variables.scss";

.inputField {
  width: 100%;

  input {
    width: 100%;
    padding: 0.5rem 0.1rem;
    border: none;
    border-bottom: solid 2px #ccc;
    outline: none;
    margin: 1rem auto;
    font-size: 1rem;
  }

  input:focus {
    border-bottom: solid 2px orange;
  }
}
