@import "../../scss/mixins.scss";
@import "../../scss/variables.scss";

.menubar {
  transition: all 1s;

  @include wrapper;
  background-color: white;
  position: fixed;
  top: 0;
  right: 0;
  min-height: 100vh;
  width: 75vw;
  z-index: 300;

  .heading {
    @include flexbox(space-between, center);
    margin-bottom: 1rem;
  }

  .links {
    display: flex;
    flex-direction: column;
  }

  .link {
    padding: 1rem 0;
    color: $color--five;
  }
}

@media screen and (min-width: 500px) {
  .menubar {
    width: 45vw;
  }
}

@media screen and (min-width: 700px) {
  .menubar {
    width: 40vw;
  }
}

@media screen and (min-width: 1125px) {
  .menubar {
    width: 30vw;
  }
}
