@import "../../scss/mixins.scss";

.wishList {
  @include wrapper;
  transition: all 1s;
  min-height: 100vh;
  margin-top: 11vh;

  .titleContainer {
    @include titleContainer;
  }

  .emptyMsg {
    @include empty;
  }

  .checkout {
    @include flexbox(space-between, center);
    margin: 1rem 0;
  }
}

@media screen and (min-width: 600px) {
  .wishList {
    width: 80vw;
    margin: auto;
    margin-top: 12vh;
  }
}

@media screen and (min-width: 800px) {
  .wishList {
    width: 70vw;
  }
}

@media screen and (min-width: 1125px) {
  .wishList {
    width: 50vw;
  }
}
