@import "../../scss/mixins.scss";

.popup {
  width: 80vw;
  margin: auto;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2000;
  background-color: white;
  border-radius: 3px;
}

@media screen and (min-width: 500px) {
  .popup {
    width: 60vw;
    margin: auto;
    text-align: center;
  }
}

@media screen and (min-width: 700px) {
  .popup {
    width: 50vw;
    margin: auto;
  }
}

@media screen and (min-width: 1125px) {
  .popup {
    width: 30vw;
  }
}
