@import "../../scss/variables.scss";
@import "../../scss/mixins.scss";

.signup__container {
  @include signFormContainer;

  .signup {
    width: 80vw;
    min-height: 30vh;
    margin: auto;
    background-color: white;
    border-radius: 0.3rem;
    padding: 1.6rem;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    transition: all 1s;

    .wrapper {
      text-align: left;
      padding: 1rem 0;
    }

    .errorMsg {
      color: red;
      font-size: 0.85rem;
      opacity: 0;
    }

    .displayError {
      opacity: 1;
    }

    .btnWrapper,
    .alternativeBtn {
      margin-top: 1rem;
      text-align: center;
    }

    p {
      text-align: center;
      margin: 1rem 0;
    }

    span {
      color: rgb(0, 153, 255);
      cursor: pointer;
      font-weight: 600;
    }
  }

  .fa-arrow-alt-circle-left {
    margin: 1rem 0 0 1rem;
    cursor: pointer;
  }
}

@media screen and (min-width: 500px) {
  .signup__container {
    .signup {
      width: 70vw;
      margin: auto;
    }
  }
}

@media screen and (min-width: 700px) {
  .signup__container {
    .signup {
      width: 50vw;
    }
  }
}

@media screen and (min-width: 1125px) {
  .signup__container {
    .signup {
      width: 40vw;
    }
  }
}
