@import "../../scss/variables.scss";
@import "../../scss/mixins.scss";

.category {
  @include wrapper;

  .category__heading {
    @include titleContainer;
  }

  .category__content {
    display: grid;
    grid-template-columns: repeat(2, 49%);
    gap: 2%;
  }

  .image__container {
    width: 100%;
    height: 100%;
    border-radius: 0.3rem;
    overflow: hidden;
    border: solid 0.1px #ccc;

    &:hover {
      cursor: pointer;
      box-shadow: 0 0 6px 0 #ccc;
    }
  }

  img {
    width: 100%;
    height: 80%;
  }

  .item__name {
    text-align: center;
    font-size: 0.85rem;
    align-self: center;
  }
}

@media screen and (min-width: 500px) {
  .category {
    .item__name {
      font-size: 1.1rem;
      padding-top: 0.6rem;
    }
  }
}

@media screen and (min-width: 800px) {
  .category {
    width: 80vw;
    margin: auto;

    .item__name {
      font-size: 1.2rem;
      padding-top: 0.6rem;
    }
  }
}

@media screen and (min-width: 1000px) {
  .category {
    width: 80vw;
    margin: auto;

    .item__name {
      font-size: 1.2rem;
      padding-top: 0.8rem;
    }
  }
}
