.cover__section {
  min-height: 70vh;
  text-align: center;
  background-position: center;
  background-size: cover;
  backdrop-filter: blur(0.2rem);
  margin-top: 11vh;

  .overlay {
    background-color: #00000086;
    height: 70vh;
  }

  .content {
    width: 80vw;
    margin: auto;
    padding-top: 10rem;
    color: white;
  }

  .btn__container {
    margin-top: 2rem;
  }

  h2 {
    font-size: 2rem;
  }

  p {
    font-size: 1.1rem;
  }
}
