@import "../../scss/mixins.scss";

.checkout__form {
  margin: auto;
  transition: all 1s;
  padding: 2rem;

  .title {
    @include flexbox(space-between, center);

    p {
      font-size: 1.2em;
    }
  }

  .form {
    width: 100%;
  }

  .valid {
    @include flexbox(space-between, center);
  }
}
