.primary,
.secondary {
  width: 100%;
  padding: 0.8rem 0;
  background-color: rgb(255, 153, 0);
  color: white;
  border: none;
  border-radius: 3px;
  font-weight: bold;
  font-size: 0.95rem;
  outline: none;
  cursor: pointer;
  box-shadow: 0 0 3px #ccc;

  &:active {
    background-color: rgb(255, 190, 92);
  }
}

.secondary {
  background-color: white;
  color: rgb(255, 153, 0);

  &:active {
    background-color: #eeeeee;
  }
}
