@import "../../scss/mixins.scss";

.favourites {
  min-height: 100vh;
  margin-top: 11vh;
  transition: all 1s;

  @include wrapper;

  .titleContainer {
    @include titleContainer;
  }

  .emptyMsg {
    @include empty;
  }
}

@media screen and (min-width: 600px) {
  .favourites {
    width: 80vw;
    margin: auto;
    margin-top: 12vh;
  }
}

@media screen and (min-width: 800px) {
  .favourites {
    width: 70vw;
  }
}

@media screen and (min-width: 1125px) {
  .favourites {
    width: 50vw;
  }
}
