*,
*::before,
*::after {
  margin: 0;
  box-sizing: inherit;
}

body {
  box-sizing: border-box;
  color: #363434;
  font-family: "Mulish", sans-serif;
  /* font-family: "Oswald", sans-serif; */
  /* font-family: "Open Sans", sans-serif; */
}

a {
  text-decoration: none;
}

